@import url(https://fonts.googleapis.com/css?family=Montserrat);
html, body, #root {
    height: 100%;
    width: 100%;
    margin: 0;
    padding: 0;
}

#splash-page-wrapper {
    position: relative;
    width: 100%;
    height: 100%;
}

#splash-page {
    width: 100%;
    margin: 0 auto;
    height: 100%;
    position:relative;
    overflow: hidden;
    white-space: nowrap;
}


